import { contact } from "../Data";
function Contact() {
  const FormSubmit = () => {
    return "$('#cform').submit(); return false;";
  };

  return (
    <div className="card-inner active" id="contacts-card">
      <div className="row card-container">
        {/* Card Wrap */}
        <div className="card-wrap col col-m-12 col-t-12 col-d-8 col-d-lg-6">
          {/* <!-- Map --> */}
          {/* <div className="card-image col col-m-12 col-t-12 col-d-4 col-d-lg-6">
            <div className="map" id="map"></div>
          </div> */}

          {/* Inner Top */}
          <div className="content inner-top">
            <div className="row">
              <div className="col col-m-12 col-t-12 col-d-12 col-d-lg-12">
                <div className="title-bg">Contacts</div>
              </div>
            </div>
          </div>

          {/* Contacts Info */}
          <div className="content contacts-info">
            <div className="row">
              <div className="col col-m-12 col-t-12 col-d-12 col-d-lg-12">
                {/* <!-- title --> */}
                <div className="title">
                  <span>Get</span> in Touch
                </div>
              </div>
            </div>

            {/* <!-- contacts items --> */}
            <div className="row contacts-items">
              {/* <!-- contacts item --> */}
              {contact.map((item, id) => {
                const { title, icon, content, link } = item;
                return (
                  <div
                    key={id}
                    className="col col-m-12 col-t-6 col-d-6 col-d-lg-6"
                  >
                    <a href={link ? `${link}` : null}>
                      <div className="contacts-item card-box">
                        <div className="icon">
                          <i className={`la ${icon}`}></i>
                        </div>
                        <div className="name">{title}</div>
                        <p>{content}</p>
                      </div>
                    </a>
                  </div>
                );
              })}
            </div>
          </div>

          {/* Contacts Form */}
          <div className="content contacts-form" id="ContactForm">
            <div className="row">
              <div className="col col-m-12 col-t-12 col-d-12 col-d-lg-12">
                {/* <!-- title --> */}
                <div className="title">
                  <span>Contact</span> Form
                </div>
              </div>
            </div>
            <div className="row">
              <div className="col col-m-12 col-t-12 col-d-12 col-d-lg-12">
                {/* <!-- form --> */}
                <div className="contact_form card-box">
                  <form id="cform" method="post">
                    <div className="row">
                      <div className="col col-m-12 col-t-6 col-d-6 col-d-lg-6">
                        <div className="group-val">
                          <input
                            type="text"
                            name="name"
                            placeholder="Full Name"
                          />
                        </div>
                      </div>
                      <div className="col col-m-12 col-t-6 col-d-6 col-d-lg-6">
                        <div className="group-val">
                          <input
                            type="text"
                            name="email"
                            placeholder="Email Address"
                          />
                        </div>
                      </div>
                      <div className="col col-m-12 col-t-12 col-d-12 col-d-lg-12">
                        <div className="group-val">
                          <textarea
                            name="message"
                            placeholder="Your Message"
                          ></textarea>
                        </div>
                      </div>
                    </div>
                    <div className="align-left">
                      <a href="/#" className="button" onClick={FormSubmit}>
                        <span className="text">Send Message</span>
                        <span className="icon">
                          <i className="la la-arrow-right"></i>
                        </span>
                      </a>
                    </div>
                  </form>
                  <div className="alert-success">
                    <p>Thanks, your message is sent successfully.</p>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
export default Contact;
